.buttons-group {
	flex: 1;
	display: flex;
	justify-content: flex-end;
	margin: 0 -9px;
}

.button {
	height: 42px;
	min-width: 42px;
	justify-content: center;
	border-radius: 21px;
	border: 1px solid #efe4c4;
	padding: 0 20px;
	display: flex;
	align-items: center;
	cursor: pointer;
	margin: 0 9px;
	box-sizing: border-box;
}
.button.primary {
	background-color: #efe4c4;
}
.button.no-padding {
	padding: 0;
}
.button-icon {
	fill: #efe4c4;
}
.button.primary .button-icon {
	fill: #1b2433;
}

.button-icon-left {
	margin-right: 6px;
}

.button-icon-right {
	margin-left: 6px;
}

.button-text {
	color: #efe4c4;
	font-size: 16px;
	font-weight: 500;
}

.button.primary .button-text {
	color: #1b2433;
}

.button-icon {
}

.button.secondary {
	filter: grayscale(1);
	opacity: 0.8;
}

.button:active {
	opacity: 0.6;
}

.button.disabled {
	opacity: 0.5;
	cursor: default;
}
