.input {
	background-color: #202a3b;
	border: 1px solid #353e52;
	border-radius: 3px;
	box-sizing: border-box;
	height: 54px;
	font-size: 17px;
	padding: 0 16px;
	color: #f7f9f1;
	width: 100%;
	outline-width: 0;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

/* Firefox */
input[type="number"] {
	-moz-appearance: textfield;
}

.input[disabled] {
	color: #f7f9f160;
}

.react-autosuggest__container {
	position: relative;
	z-index: 1;
}

.react-autosuggest__suggestions-container--open {
	background-color: #202a3b;
	color: white;
}

.react-autosuggest__suggestions-container--open ul,
.react-autosuggest__suggestions-container--open li {
	list-style: none;
	padding: 0;
}

.react-autosuggest__suggestions-container--open ul {
	border: 1px solid #353e52;
	border-bottom: none;
	position: absolute;
	top: 40px;
	background-color: #353e52;
	box-shadow: 0 0 10px #00000030;
	border-radius: 3px;
	width: 100%;
	max-height: 220px;
	overflow: auto;
}

.react-autosuggest__suggestions-container--open li {
	height: 44px;
	display: flex;
	align-items: center;
	padding: 0 16px;
	border-bottom: 1px solid #202a3b;
}

.react-autosuggest__suggestion--highlighted {
	background-color: #202a3b;
	cursor: pointer;
}
