.footer {
	height: 100px;
	display: flex;
	border-top: 1px solid #323b4c;
	height: 94px;
	padding: 0 28px;
	align-items: center;
	justify-content: center;
	position: relative;
}

.footer.loading {
	border-color: transparent;
}

.footer-loader {
	position: absolute;
	width: 100%;
	top: 0;
	display: flex;
	top: -2.5px;
	display: none;
}

.footer.loading .footer-loader {
	display: flex;
}
