.steps {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.steps-text {
	color: #c5c7c0;
	margin-bottom: 8px;
	font-feature-settings: "tnum";
	font-variant-numeric: tabular-nums;
}

.steps-nav {
	display: flex;
}

.steps-nav-item {
	width: 16px;
	height: 16px;
	border-radius: 8px;
	margin-right: 8px;
	box-sizing: border-box;
	border: 1px solid white;
}

.steps-nav-item.passed {
	border-color: #efe4c4;
	background-color: #efe4c4;
}
